const hamburger = document.querySelector('.hamburger');
const mobile = document.getElementById('mobile-container');
const main = document.getElementById('main');
const header = document.getElementById('header');
const footer = document.getElementById('footer');
const close = document.querySelector('#mobile-container .close');

const company = document.querySelector('#mobile-container .menu-wrapper ul li:first-child a');
const contact = document.querySelector('#mobile-container .menu-wrapper ul li:last-child a');

if (hamburger !== null) {
    if (matchMedia('screen and (max-width:1023px)').matches) {
        hamburger.addEventListener('click', function() {
            mobile.classList.add('active');
            main.classList.add('flip');
            header.classList.add('flip');
            footer.classList.add('flip');
        });

        close.addEventListener('click', function() {
            mobile.classList.remove('active');
            main.classList.remove('flip');
            header.classList.remove('flip');
            footer.classList.remove('flip');
        });

        //        
        company.addEventListener('click', function() {
            mobile.classList.remove('active');
            main.classList.remove('flip');
            header.classList.remove('flip');
            footer.classList.remove('flip');
        });
        contact.addEventListener('click', function() {
            mobile.classList.remove('active');
            main.classList.remove('flip');
            header.classList.remove('flip');
            footer.classList.remove('flip');
        });
    }
}
