const index = document.getElementById('index');
const visibilityButton = Array.from(document.querySelectorAll('button.swiper-trigger-btn'));

if (index !== null) {
    visibilityButton.map(button => {
        button.addEventListener('click', function(e) {
            if (e.currentTarget.classList.contains('visibility')) {
                e.currentTarget.classList.remove('visibility');
                e.currentTarget.classList.add('hide');
            } else {
                e.currentTarget.classList.add('visibility');
                e.currentTarget.classList.remove('hide');
            }
        });
    });
}
